html, body {
  height: 100%;
  margin: 0;
  padding: 0;
  overflow: hidden; }

html {
  font-family: roboto, sans-serif;
  -webkit-tap-highlight-color: transparent;
  background: url("data:image/svg+xml,%3Csvg%20xmlns%3D%22http%3A%2F%2Fwww.w3.org%2F2000%2Fsvg%22%20viewBox%3D%220%200%202%202%22%3E%3Cpath%20d%3D%22M1%202V0h1v1H0v1z%22%20fill-opacity%3D%22.05%22%2F%3E%3C%2Fsvg%3E");
  background-size: 16px 16px;
  background-color: #f2f2f2;
  transition: background-color 0.2s ease-in-out; }

pre,
code,
kbd,
samp,
tt {
  font-family: monospace,monospace;
  font-size: 1em; }

button::-moz-focus-inner, textarea::-moz-focus-inner {
  border: 0;
  padding: 0; }

.main-menu {
  position: absolute;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  z-index: 3;
  font-family: sans-serif; }
  .main-menu.hide-font {
    color: transparent; }
    .main-menu.hide-font .menu-footer-notes {
      color: transparent; }
  .main-menu.use-font {
    font-family: inherit; }
  .main-menu.hidden {
    pointer-events: none; }
  .main-menu .menu {
    position: absolute;
    top: 0;
    left: 0;
    bottom: 0;
    min-width: 264px;
    width: 40%;
    max-width: 340px;
    background: #fff;
    box-shadow: 3px 0 16px rgba(0, 0, 0, 0.4);
    will-change: transform;
    overflow-x: hidden;
    overflow-y: auto;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-flow: column;
    flex-flow: column; }
    .main-menu .menu.transition {
      -webkit-transition: -webkit-transform 0.2s ease-out;
      transition: transform 0.2s ease-out; }
    .main-menu .menu.hidden {
      -webkit-transform: translateX(-110%);
      transform: translateX(-110%); }
    .main-menu .menu > * {
      min-height: -webkit-min-content;
      min-height: -moz-min-content;
      min-height: min-content; }
  .main-menu .overlay {
    position: absolute;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
    background: rgba(0, 0, 0, 0.4);
    will-change: opacity; }
    .main-menu .overlay.transition {
      -webkit-transition: opacity 0.2s ease-in-out;
      transition: opacity 0.2s ease-in-out; }
    .main-menu .overlay.hidden {
      opacity: 0; }
  .main-menu ul {
    margin: 0;
    padding: 0; }
  .main-menu li {
    display: block;
    margin: 0;
    padding: 0; }
  .main-menu .menu-item {
    padding: 0 16px;
    width: 100%;
    height: 48px;
    box-sizing: border-box;
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
    align-items: center; }
    .main-menu .menu-item:link, .main-menu .menu-item:visited {
      color: inherit;
      text-decoration: none; }
    .main-menu .menu-item:focus, .main-menu .menu-item:hover {
      outline: none;
      background: rgba(0, 0, 0, 0.1); }
    .main-menu .menu-item .icon {
      width: 24px;
      height: 24px;
      opacity: 0.54;
      margin-right: 32px;
      -webkit-flex: none;
      flex: none; }
    .main-menu .menu-item .spinner {
      -webkit-flex: none;
      flex: none;
      margin: 0 0 0 auto; }
  .main-menu .menu-input {
    cursor: pointer; }
    .main-menu .menu-input .input-area {
      position: relative;
      white-space: nowrap; }
    .main-menu .menu-input .label-txt {
      position: absolute;
      left: 0;
      top: 0; }
    .main-menu .menu-input textarea,
    .main-menu .menu-input input {
      display: block;
      font: inherit;
      border: none;
      background: none;
      padding: 0;
      margin: 0;
      opacity: 0;
      cursor: inherit;
      resize: none;
      width: 100%;
      height: 1.3em;
      overflow: hidden; }
      .main-menu .menu-input textarea:focus,
      .main-menu .menu-input input:focus {
        outline: none;
        opacity: 1; }
        .main-menu .menu-input textarea:focus + .label-txt,
        .main-menu .menu-input input:focus + .label-txt {
          opacity: 0;
          pointer-events: none; }
  .main-menu .select-file-input {
    display: none; }

.menu-footer-notes {
  padding: 0 16px;
  margin-top: auto;
  text-align: center;
  font-size: 0.9rem;
  color: #8E8E8E; }
  .menu-footer-notes a:link,
  .menu-footer-notes a:visited {
    color: inherit;
    text-decoration: underline; }
  .menu-footer-notes a:hover,
  .menu-footer-notes a:active {
    color: #000; }

.preloader {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(0, 0, 0, 0.7);
  z-index: 3;
  color: #fff;
  opacity: 0;
  font-family: sans-serif;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-flow: column;
  flex-flow: column;
  -webkit-align-items: center;
  align-items: center;
  -webkit-justify-content: center;
  justify-content: center;
  -webkit-transition: opacity 0.2s ease-out;
  transition: opacity 0.2s ease-out; }
  .preloader.active {
    opacity: 1; }
    .preloader.active .spinner {
      display: block; }
  .preloader .spinner {
    display: none;
    border-color: #fff; }

.unbutton {
  display: inline-block;
  background: none;
  border: none;
  padding: 0;
  margin: 0;
  font: inherit;
  cursor: pointer;
  white-space: nowrap; }

.toolbar, .main, .drop-overlay {
  display: none; }
